<template>
    <div page-index>
        <CustomVue name="小舞" />
        <CustomVue name="荣荣" />
        <CustomVue />
        <hr />
        <div v-card>
            <p>关于props属性</p>
            <div>
                <pre>
props本质是组件之间传递数据问题
使用时在需要传递数据的组件上写对应的属性, 并且在接收数据组件内部使用props属性接收即可
例:
  &lt;Custom name="小舞"/&gt;
  Custom.vue内部
  export default {
    props: ['name']
  }
以上是默认传递方式
传递Number类型数据方式
例1:
  &lt;Custom :num="18"/&gt;
  只需在属性前加上[:], 让其作为表达式传递
例2:
  &lt;Custom :num="18"/&gt;
  Custom.vue组件内部
  export default {
    props: {
        num: Number
    }
  }
  在接收组件内部props属性中定义数据的类型
  此方式可以预防组件传递数据时[:]忘记携带的情况
深度写法:
例:
  Custom.vue组件内部
  export default {
    props: {
        name: {
            // 类型
            type: String,
            // 必填项
            required: true,
            // 默认值
            default: '小舞'
        }
    }
  }
注1:
  不建议修改props属性接收到的数据
  数据可以被修改, 但会有警告
  Avoid mutating a prop directly since the value will be overwritten whenever the parent component re-renders.
杠:
  q: 我就想改 你就说能不能行😡
  a: 当然, 可以通过替身术的方式进行修改😎
  q: 啥替身术啊, 能不能说人话!
  a: 话术都听不懂, 还学人杠精😒就是复制props的值到data里面, 修改时修改data中的值即可
  a: 看好了, 我只展示一次
  Custom.vue组件内部

  &lt;template&gt;
    &lt;&#33;-- 显示时只显示内部定义的变量 --&gt;
    &#123;tempName&#125;
  &lt;template/&gt;

  export default {
    props: ['name'],
    data() {
        return {
            // 使用一个变量接收传递的参数
            tempName: this.name
        }
    },
    methods: {
        changeName() {
            // 修改时, 只修改内部定义的值即可
            this.tempName = '修改的值'
        }
    }
  }
注2:
  若组件内部已经定义了和传递属性名字一样的属性, 以props接收到的值为准
注3: 传递的属性名称不能和Vue内部属性重复 如: key, ref
                </pre>
            </div>
        </div>
    </div>
</template>

<script>
import CustomVue from '@/common/Custom.vue';

export default {
    name: 'Props',
    components: {
        CustomVue,
    },
    data() {
        return {};
    },
};
</script>

