<template>
    <div :style="{ color: color }">
        <p>{{ message }}</p>
        <p>
            组件传递数据:
            <span style="text-decoration: underline #f3a694">{{ name }}</span>
        </p>
        <p>
            <button
                type="button"
                @click="changeName">
                尝试修改接收到的数据
            </button>
        </p>
    </div>
</template>

<script>
export default {
    name: 'Custom',
    props: {
        name: {
            default: '未传递',
        },
        color: {
            default: 'unset',
        },
    },
    data() {
        return {
            message: '我是Custom组件',
            number: 18,
        };
    },
    methods: {
        changeName() {
            this.name = '修改后的值';
        },
    },
};
</script>

<style scoped></style>
